.petal {
  height: 50px;
  width: 50px;
  border: 1px solid rgba(148, 148, 148, 0.2);
  border-radius: 71% 29% 90% 10% / 96% 25% 75% 4%;
  box-sizing: border-box;
  animation: sway 5s infinite;
  box-shadow: 8px 5px 15px 5px rgba(0, 0, 0, 0.3);
}

